@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Boogaloo&family=Lato&family=Meow+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Boogaloo&family=Lato&family=Lora&family=Meow+Script&display=swap);
body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fdf0e2 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title {
  text-align: center;
  font-size: 50px;
}
.category-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 5%;
}
.name {
  font-size: 30px;
  font-weight: 900;
}

.desc {
  font-size: 20px;
}

#nav {
  display: flex;
  flex-direction: row;
  margin: 10px;
}
.b-name {
  font-family: 'Boogaloo', cursive;
  font-family: 'Lato', sans-serif;
  font-family: 'Meow Script', cursive;
  font-size: 30px;
}
#nav div {
  display: flex;
  flex-direction: row;
  margin-left: auto;
  padding-left: 40px;
  font-weight: bold;
}

.footer-lay {
  background-color: #fdf0e2;
  flex: 1 1;
  bottom: 0;
  /* position: fixed; */
  width: 100%;
}
.ab-img {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-family: sofia-pro, sans-serif;
  width: 50%;
  margin: 10px;
  font-weight: 200;
}
.about-us {
  padding-left: 30px;
}
.madeby {
  text-align: center;
  padding-bottom: 10px;
}

.profile {
  display: flex;
  justify-content: center;
  align-items: center;
}
.total {
  padding-left: 100px;
  font-size: x-large;
}
.cart-image {
  height: 300px;
}
.gifts {
  display: flex;
  justify-content: center;
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.buttons button {
  cursor: pointer;
}

.each-product {
  margin: 10px;
  padding: 5px;
  border-radius: 5%;
}
.name {
  font-size: 30px;
  font-weight: 900;
  padding: 10px;
}
.desc {
  font-size: 20px;
  font-weight: 300;
}
.pname {
  padding-left: 10px;
}

.home {
  text-align: center;
}
.detai-price {
  font-size: large;
}
.category-name {
  padding: 10px 0 0 20px;
  font-size: larger;
}
.arrow {
  height: 20px;
  padding-right: 5px;
}

